import { Component } from '@angular/core';
import { IonicPage, NavController, App, NavParams, ViewController, LoadingController } from 'ionic-angular';
import { SyndicateService } from '../../providers/syndicate-service';
import { AppSoundProvider } from '../../providers/app-sound/app-sound';
import { TabsPage } from '../tabs/tabs';
import { Params } from '../../services/params';

@IonicPage()
@Component({
  selector: 'page-create-syndicate5',
  templateUrl: 'create-syndicate5.html'
})
export class CreateSyndicate5Page {
  private termsData: any;
  constructor(public navCtrl: NavController, public navParams: NavParams,
    public appSound: AppSoundProvider,
    private viewCtrl: ViewController, public _syndService: SyndicateService, public loadingCtrl: LoadingController,
    public app: App, public params: Params) { 
      
    }

  ionViewDidLoad() {
    var id = localStorage.getItem('synd_id');
    this.getTerms(id);
  }
  ionViewWillEnter() {
    this.viewCtrl.showBackButton(false);
  }
  close() {
    this.appSound.play('menuClick');
    this.navCtrl.popToRoot().then(() => {
      this.params.goTab(2)
    })
  }
  next() {
    this.navCtrl.push('ChooseNumberPage');
  }
  aggreement() {
    this.navCtrl.push('AgreementPage', { 'tandc': this.termsData.terms_contents, 'agree': this.termsData.agreement });
  }
  getTerms(id: any) {
    this.appSound.play('buttonClick');
    let loader = this.loadingCtrl.create({
      spinner: 'hide',
      content: `<img src="assets/vid/blue_bg.gif" style="height:100px!important">`,
    });
    loader.present().then(() => {
      this._syndService.getTerms(id).subscribe((res) => {
        loader.dismiss();
        this.termsData = res.response[0].get_private_syndicate_terms.response
        console.log(res);
      })
    })

  }

}



